<template>
  <div class="wrap">
    <header>header</header>

    <div class="main-nav">
      <bootNavbar/>
    </div>

    <div class="content">
      <div class="content-nav">
        <bootNavbar/>
      </div>
      <div class="content-main">
        <form action="/freeBoard/setList" method="post">
          <input type="text" name="contentTest01">
          <button type="submit">test</button>
        </form>
        <button @click="getPhotos">데이터확인4</button>
        <!-- test -->
        <div class="form-group">
                <input type="text" v-model="name" placeholder="이름을 입력합니다"/>
                <input type="text" v-model="comment" placeholder="내용을 입력합니다"/>
                <button @click="addContact">data 1건 추가</button>
            </div>
      </div>
    </div>
    
    <div style="clear: both"></div>
    <footer>footer</footer>
    {{testjson}}
  </div>
  
 
</template>

<script>
import axios from 'axios'
import 'bootstrap'
import 'bootstrap/dist/css/bootstrap.min.css'
import bootNavbar from './components/bootNavbar.vue'
import testjson from './json/testjson.js'

axios.defaults.baseURL = 'http://localhost:8000'
axios.defaults.headers.post['Content-Type'] = 'application/json;charset=utf-8'
axios.defaults.headers.post['Access-Control-Allow-Origin']='*';

export default {
  name: 'App',
  data() {
    return {
      testjson,
    }
  },
  components: {
    bootNavbar,
  },
  methods: {
    getPhotos: function(){
      axios.get("/freeBoard/list")
      .then(result=>{
        console.log(result.data)
        this.testjson = result.data
      }).catch(err=>{window.alert("에러내용\n" + err)});
    },

    addContact : function() {
            axios.post('/freeBoard/setList', 
                { name:this.name, comment:this.comment}
            ).then(response => {
                console.warn(response)
                this.result = response.data
                this.no = response.data.no
            }).catch((ex) => {
                console.warn("ERROR!!!!! : ",ex)
            })
        },
    
 
  },
}
</script>

<style>
@import url(./css/index.css);
